






















import Vue from 'vue';
import {BusinessProcess} from "@/models/BusinessProcess";
import {getBusinessProcesses} from "@/api/Process"; // @ is an alias to /src

interface ViewData {
  business_processes: Array<BusinessProcess>
  loading: boolean
}

export default Vue.extend({
  name: 'View',
  components: {},
  data: (): ViewData => {
    return {
      business_processes: [],
      loading: false
    }
  },
  mounted() {
    const vm = this
    vm.loading = true
    getBusinessProcesses().then(r => {
      vm.business_processes = r.data
    }).finally(() => {
      vm.loading = false
    });
  },
  computed: {
    is_bp_empty() {
      if (this.$data.loading) {
        return false;
      }
      return this.$data.business_processes.length == 0;
    }
  }
});
